<template>
    <div class="tagging-editor">
        <navigate :title="title" position="absolute">
            <!--<mu-button slot="right" icon>-->
            <!--<mu-icon size="18" value="phone"/>-->
            <!--</mu-button>-->
        </navigate>
        <div class="scroll-wrapper">
            <mu-form :model="formReport" label-position="left" label-width="80" v-if="mode === 'report'">
                <div class="p-15 fz-14">{{Date.now() | formatDate()}}</div>
                <!--<mu-form-item class="mn p-15 paper border-1px-b" label="故障位置" prop="title">-->
                <!--<mu-text-field v-model="formReport.title" placeholder="请输入故障位置 ..." solo/>-->
                <!--</mu-form-item>-->
                <mu-form-item class="mn p-15 paper" label="上报类型" prop="type" @click.native="open = true">
                    <mu-row class="mn w h" align-items="center" justify-content="end">
                        <span class="color-primary fz-14"
                              v-if="formReport.typeId">{{type(formReport.subType).name}}</span>
                        <span class="color-primary fz-12" v-else>请选择</span>
                    </mu-row>
                </mu-form-item>
                <mu-form-item class="mt-20 paper" prop="image" label-position="top" label-width="100%">
                    <div class="p-15 border-1px-b" slot="label">照片</div>
                    <div class="picture-wrapper pt-25">
                        <div class="picture border-1px" v-if="formReport.images.length < 9" @click="handlerPicture">
                            <mu-icon class="append" value="add"/>
                        </div>
                        <div class="picture" v-for="(item, index) in formReport.images"
                             :style="{backgroundImage: `url('${preHost+item}')`}"
                             @click="handlerBrowse(index)">
                            <mu-button class="scale-small" color="#f44336" size="16"
                                       @click.stop="deletePicture(item, index)"
                                       fab>
                                <mu-icon value="delete"/>
                            </mu-button>
                        </div>
                    </div>
                </mu-form-item>
                <mu-form-item class="paper" prop="describe" label-position="top" label-width="100%">
                    <div class="p-15 border-1px-b" slot="label">描述</div>
                    <div class="p-15 w">
                        <mu-text-field v-model="formReport.describe"
                                       placeholder="请输入描述信息 ..."
                                       :rows="8"
                                       :max-length="300"
                                       full-width
                                       multi-line
                                       solo/>

                    </div>
                </mu-form-item>
                <mu-button class="db ma w90 background-primary mb-30" color="#fff" data-mu-loading-size="24"
                           v-loading="loading"
                           @click="handlerSubmit"
                           :disabled="loading"
                           flat>提交
                </mu-button>
            </mu-form>
        </div>

        <mu-bottom-sheet class="scroll-y mh50" :open.sync="open" lock-scroll>
            <mu-list @item-click="open = false">
                <mu-list-item v-for="(item, index) in types" :key="index"
                              @click.native="handlerToggleType(item.id)"
                              button>
                    <mu-list-item-action>
                        <mu-icon value="room"
                                 :color=" formReport.subType === item.id ? 'orange' : ''"/>
                    </mu-list-item-action>
                    <mu-list-item-content>
                        <mu-list-item-title>{{item.name}}</mu-list-item-title>
                    </mu-list-item-content>
                </mu-list-item>
            </mu-list>
        </mu-bottom-sheet>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {formatDate} from '../../../common/utils/time'
    import {saveOrUpdateILabel, queryReportDetail, saveFaultReport} from "../../../api/personal"
    import {queryTypeDictByFid} from "../../../api/search"
    import {getPictures, getBase64} from "../../../common/js/cordova/camera"
    import {uploadBase64} from "../../../api/sys"
    import ImagePreview from "vant/lib/image-preview"

    const name = 'SELECTION_and_TAGGING_and_REPORT_EDITOR'

    export default {
        name,
        data() {
            return {
                open: false,
                loading: false,
                defaultList: [],
                taggingTypes: [],
                reportTypes: [],
                preHost:this.$images+'\\',
                formReport: {
                    images: '',
                    type: '',
                    longitude: "",
                    latitude: "",
                    subType: ""
                }
            }
        },
        props: {
            id: {
                type: String,
                default: ''
            },
            longitude: {
                type: [String, Number],
                default: 0
            },
            latitude: {
                type: [String, Number],
                default: 0
            }
        },
        computed: {
            title() {

                return '修改上报'
            },
            mode() {

                return 'report'
            },
            types() {
                return this.reportTypes

            }
        },
        created() {

            queryTypeDictByFid('031').then(res => {
                this.reportTypes = res
            })
        },
        activated() {
            console.log(" this.$images ", this.$images)
            this.preHost = this.$images+'\\'
            this.formReport = {
                images: [],
                type: '',
                longitude: "",
                latitude: ""
            }

            queryReportDetail(this.id).then(res => {
                this.formReport = res
            })

            this.defaultList = []

        },
        methods: {
            /***
             * 点击选择图片
             */
            handlerPicture() {
                getPictures(9, this.defaultList).then(images => {
                    this.formReport.images = []
                    this.defaultList = []
                    this.preHost =""
                    Array.prototype.forEach.call(images, item => {
                        this.defaultList.push(item.path)
                        getBase64(item).then(res => {
                            this.formReport.images.push(`data:image/jpeg;base64,${res.thumbnailBase64}`)
                        })
                    })
                })
            },
            /***
             * 点击浏览图片
             */
            handlerBrowse(index) {
                var perviews = [];
                for(var j = 0,len=this.formReport.images.length; j < len; j++) {
                    perviews.push(this.preHost+this.formReport.images[j])
                }

                ImagePreview({
                    images: perviews,
                    startPosition: index
                })
            },
            /***
             * 删除图片
             */
            deletePicture(item, index) {
                this.formReport.images.splice(index, 1)
                this.defaultList.splice(index, 1)
            },
            // 切换类型
            handlerToggleType(typeId) {

                this.formReport.subType = typeId

            },
            // 类型
            type(value) {
                const result = this.types.find(item => {
                    return item.id === value
                })

                return result || {}
            },
            // 提交
            handlerSubmit() {
                this.loading = true

                switch (this.mode) {
                    case  'report':
                        return this.handlerReport()
                    default:
                        this.$toast('当前模式不可使用！')
                }

            },

            // TODO: 添加&修改故障上报
            async handlerReport() {
                let images = []
                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在上传 ...',
                    duration: 0
                })

                if (this.formReport.images.length) {
                    images = await Promise.all(this.formReport.images.map(image => {
                        if (image.split("image/jpeg;base64").length > 1) {
                            return uploadBase64(image)
                        }else{
                            return image
                        }
                    }))
                }

                const data = {
                    describe: this.formReport.describe,
                    images: images,
                    type: this.formReport.subType,
                    longitude: this.formReport.longitude || this.longitude,
                    latitude: this.formReport.latitude || this.latitude
                }

                if (this.id) {
                    data.id = this.id
                }

                if (!data.type) {
                    this.loading = false
                    this.$toast("请选择上报类型")
                    return
                }

                saveFaultReport(data).then(res => {

                    this.$toast('修改成功')

                    this.$router.back()

                    this.loading = false

                    window.Arcgis && window.Arcgis.refreshMapLayers()

                    this.$emit('update')
                }).catch(error => {
                    this.$toast('修改失败')

                    this.loading = false
                })
            }
        },
        components: {
            Navigate
        },
        filters: {
            formatDate: formatDate
        },
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .tagging-editor
        padding-top 46px

        .picture
            .scale-small
                top -25%
                right -30%
</style>



